/**
 * Kitchen Display System V2
 * Industrial POS design - Full screen, high frequency, touch-optimized
 */

import { useEffect, useCallback, useState } from 'react';
import { useAuthStore } from '../stores/authStore';
import { useKDSStore } from '../stores/kdsStore';
import { useNotificationStore } from '../stores/notificationStore';
import type { KitchenOrder, KitchenItemStatus, KitchenStation } from '../types/kds';
import { cn } from '../lib/utils';

type StationFilter = KitchenStation;

export default function KitchenDashboard() {
  const { user } = useAuthStore();
  const {
    activeOrders,
    fetchOrders,
    markItemStatus,
    markItemReady,
    updateOrder,
  } = useKDSStore();
  const { playSound } = useNotificationStore();

  const [stationFilter, setStationFilter] = useState<StationFilter>('all');
  const [processingItems, setProcessingItems] = useState<Set<string>>(new Set());
  const [currentTime, setCurrentTime] = useState(Date.now());

  // Update current time every second for timers
  useEffect(() => {
    const interval = setInterval(() => setCurrentTime(Date.now()), 1000);
    return () => clearInterval(interval);
  }, []);

  // Fetch orders on mount and periodically
  useEffect(() => {
    if (!user?.tenantId) return;

    fetchOrders(user.tenantId);
    const interval = setInterval(() => {
      if (user?.tenantId) {
        fetchOrders(user.tenantId);
      }
    }, 5000);

    return () => clearInterval(interval);
  }, [user?.tenantId, fetchOrders]);

  // Handle item actions
  const handleStartItem = async (orderId: string, itemId: string) => {
    const key = `${orderId}-${itemId}`;
    setProcessingItems((prev) => new Set(prev).add(key));
    try {
      await markItemStatus(orderId, itemId, 'in_progress');
    } finally {
      setProcessingItems((prev) => {
        const next = new Set(prev);
        next.delete(key);
        return next;
      });
    }
  };

  const handleReadyItem = async (orderId: string, itemId: string) => {
    const key = `${orderId}-${itemId}`;
    setProcessingItems((prev) => new Set(prev).add(key));
    try {
      await markItemReady(orderId, itemId);
      playSound('order_ready');
    } finally {
      setProcessingItems((prev) => {
        const next = new Set(prev);
        next.delete(key);
        return next;
      });
    }
  };

  const handleCompleteOrder = async (orderId: string) => {
    try {
      updateOrder(orderId, { status: 'completed' });
      playSound('order_ready');
    } catch (error) {
      console.error('Failed to complete order:', error);
    }
  };

  // Calculate order age in minutes
  const getOrderAge = (order: KitchenOrder): number => {
    const startTime = new Date(order.createdAt).getTime();
    return Math.floor((currentTime - startTime) / 1000 / 60);
  };

  // Get urgency level
  const getUrgency = (minutes: number): 'normal' | 'warning' | 'urgent' => {
    if (minutes > 20) return 'urgent';
    if (minutes > 15) return 'warning';
    return 'normal';
  };

  // Filter orders by station
  const filteredOrders = activeOrders.filter((order: KitchenOrder) => {
    if (stationFilter === 'all') return true;
    return order.items.some((item: any) => item.station === stationFilter);
  });

  // Stations
  const stations: { id: StationFilter; label: string; icon: string }[] = [
    { id: 'all', label: 'All', icon: '' },
    { id: 'grill', label: 'Grill', icon: '' },
    { id: 'wok', label: 'Wok', icon: '' },
    { id: 'fryer', label: 'Fryer', icon: '' },
    { id: 'cold', label: 'Cold', icon: '' },
    { id: 'dessert', label: 'Dessert', icon: '' },
    { id: 'beverage', label: 'Beverage', icon: '' },
  ];

  // Stats
  const activeOrdersCount = activeOrders.filter((o: KitchenOrder) => o.status !== 'completed').length;
  const pendingItems = activeOrders.reduce(
    (sum: number, o: KitchenOrder) => sum + o.items.filter((i: any) => i.status === 'pending').length,
    0
  );
  const urgentOrders = activeOrders.filter((o: KitchenOrder) => getOrderAge(o) > 20).length;

  return (
    <div className="h-screen w-screen bg-gray-950 text-white flex flex-col overflow-hidden">
      {/* Top Bar - Fixed */}
      <div className="bg-gray-900 border-b border-gray-800 px-6 py-4 flex-shrink-0">
        <div className="flex items-center justify-between">
          {/* Title */}
          <div>
            <h1 className="text-2xl font-bold">Kitchen Display System</h1>
            <div className="text-sm text-gray-400 mt-1">
              {new Date().toLocaleTimeString('en-US', {
                hour: '2-digit',
                minute: '2-digit',
                second: '2-digit',
              })}
            </div>
          </div>

          {/* Stats */}
          <div className="flex gap-6">
            <div className="text-center">
              <div className="text-3xl font-bold text-blue-400">{activeOrdersCount}</div>
              <div className="text-xs text-gray-400">Active</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-yellow-400">{pendingItems}</div>
              <div className="text-xs text-gray-400">Pending</div>
            </div>
            <div className="text-center">
              <div className="text-3xl font-bold text-red-400">{urgentOrders}</div>
              <div className="text-xs text-gray-400">Urgent</div>
            </div>
          </div>
        </div>
      </div>

      {/* Station Filter Bar - Fixed */}
      <div className="bg-gray-900 border-b border-gray-800 px-6 py-3 flex-shrink-0">
        <div className="flex gap-2 overflow-x-auto">
          {stations.map((station) => (
            <button
              key={station.id}
              onClick={() => setStationFilter(station.id)}
              className={cn(
                'px-6 py-3 rounded-lg font-semibold text-base whitespace-nowrap transition-all flex-shrink-0',
                stationFilter === station.id
                  ? 'bg-blue-600 text-white shadow-lg scale-105'
                  : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
              )}
            >
              <span className="mr-2">{station.icon}</span>
              {station.label}
            </button>
          ))}
        </div>
      </div>

      {/* Orders Grid - Scrollable */}
      <div className="flex-1 overflow-y-auto p-6">
        {filteredOrders.length === 0 ? (
          <div className="h-full flex items-center justify-center">
            <div className="text-center text-gray-500">
              <div className="text-6xl mb-4"></div>
              <div className="text-2xl font-bold">All Clear!</div>
              <div className="text-lg mt-2">No orders for {stationFilter === 'all' ? 'any station' : stationFilter}</div>
            </div>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
            {filteredOrders.map((order: KitchenOrder) => {
              const age = getOrderAge(order);
              const urgency = getUrgency(age);
              const allItemsReady = order.items.every((i: any) => i.status === 'ready');

              return (
                <div
                  key={order.id}
                  className={cn(
                    'rounded-xl p-5 transition-all',
                    urgency === 'urgent' && 'bg-red-900/40 border-2 border-red-500 shadow-lg shadow-red-500/20 animate-pulse',
                    urgency === 'warning' && 'bg-yellow-900/40 border-2 border-yellow-500',
                    urgency === 'normal' && 'bg-gray-800 border-2 border-gray-700'
                  )}
                >
                  {/* Order Header */}
                  <div className="flex items-start justify-between mb-4 pb-3 border-b border-gray-700">
                    <div>
                      <div className="text-2xl font-bold">#{order.orderNumber}</div>
                      <div className="text-sm text-gray-400 mt-1">
                        {order.source === 'zomato' ? ' Zomato' : order.source === 'swiggy' ? ' Swiggy' : ' Dine-in'}
                      </div>
                    </div>
                    <div className="text-right">
                      <div className={cn(
                        'text-3xl font-bold',
                        urgency === 'urgent' && 'text-red-400',
                        urgency === 'warning' && 'text-yellow-400',
                        urgency === 'normal' && 'text-gray-400'
                      )}>
                        {age}m
                      </div>
                      <div className="text-xs text-gray-500">ago</div>
                    </div>
                  </div>

                  {/* Items List */}
                  <div className="space-y-2 mb-4">
                    {order.items
                      .filter((item: any) => stationFilter === 'all' || item.station === stationFilter)
                      .map((item: any) => {
                        const itemKey = `${order.id}-${item.id}`;
                        const isProcessing = processingItems.has(itemKey);

                        return (
                          <div
                            key={item.id}
                            className={cn(
                              'rounded-lg p-3',
                              item.status === 'ready' && 'bg-green-900/40 border border-green-600',
                              item.status === 'in_progress' && 'bg-blue-900/40 border border-blue-600',
                              item.status === 'pending' && 'bg-gray-900/60 border border-gray-600'
                            )}
                          >
                            <div className="flex items-start justify-between gap-3">
                              <div className="flex-1 min-w-0">
                                <div className="font-semibold text-base leading-tight">
                                  {item.quantity}x {item.name}
                                </div>
                                {item.modifiers && item.modifiers.length > 0 && (
                                  <div className="text-xs text-gray-400 mt-1">
                                    {item.modifiers.join(', ')}
                                  </div>
                                )}
                                {item.specialInstructions && (
                                  <div className="text-xs text-yellow-400 mt-1 italic">
                                     {item.specialInstructions}
                                  </div>
                                )}
                              </div>

                              {/* Item Actions */}
                              <div className="flex-shrink-0">
                                {item.status === 'pending' && (
                                  <button
                                    onClick={() => handleStartItem(order.id, item.id)}
                                    disabled={isProcessing}
                                    className="bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded-lg font-semibold text-sm transition-colors disabled:opacity-50"
                                  >
                                    Start
                                  </button>
                                )}
                                {item.status === 'in_progress' && (
                                  <button
                                    onClick={() => handleReadyItem(order.id, item.id)}
                                    disabled={isProcessing}
                                    className="bg-green-600 hover:bg-green-700 px-4 py-2 rounded-lg font-semibold text-sm transition-colors disabled:opacity-50"
                                  >
                                    Ready
                                  </button>
                                )}
                                {item.status === 'ready' && (
                                  <div className="text-green-400 text-2xl"></div>
                                )}
                              </div>
                            </div>
                          </div>
                        );
                      })}
                  </div>

                  {/* Order Complete Button */}
                  {allItemsReady && (
                    <button
                      onClick={() => handleCompleteOrder(order.id)}
                      className="w-full bg-green-600 hover:bg-green-700 py-4 rounded-lg font-bold text-lg transition-colors"
                    >
                       Complete Order
                    </button>
                  )}
                </div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}
