import { useEffect, useState } from "react";
import { useMenuStore } from "./stores/menuStore";
import { Sidebar } from "./components/layout/Sidebar";
import { TopBar } from "./components/layout/TopBar";
import { OrderPanel } from "./components/layout/OrderPanel";
import { MenuGrid } from "./components/pos/MenuGrid";
import { TableManagement } from "./components/tables/TableManagement";
import { TableSelector } from "./components/tables/TableSelector";
import { MenuOnboarding } from "./components/admin/MenuOnboarding";
import { getMenuCategories, getMenuItems } from "./lib/database";

function App() {
  const setCategories = useMenuStore((state) => state.setCategories);
  const setItems = useMenuStore((state) => state.setItems);
  const setIsLoading = useMenuStore((state) => state.setIsLoading);

  const [currentView, setCurrentView] = useState("menu");
  const [showTableSelector, setShowTableSelector] = useState(false);
  const [selectedTable, setSelectedTable] = useState<number | null>(null);

  useEffect(() => {
    // Load data from SQLite database
    const loadData = async () => {
      try {
        setIsLoading(true);
        const [categories, items] = await Promise.all([
          getMenuCategories(),
          getMenuItems(),
        ]);
        setCategories(categories);
        setItems(items);
      } catch (error) {
        console.error("Failed to load data from database:", error);
      } finally {
        setIsLoading(false);
      }
    };

    loadData();
  }, [setCategories, setItems, setIsLoading]);

  const handleTableSelect = (table: any) => {
    setSelectedTable(table.number);
    setShowTableSelector(false);
  };

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Left Sidebar */}
      <Sidebar currentView={currentView} onViewChange={setCurrentView} />

      {/* Main Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {/* Top Bar - Only show on menu view */}
        {currentView === "menu" && (
          <TopBar
            onSelectTable={() => setShowTableSelector(true)}
            selectedTable={selectedTable}
          />
        )}

        {/* Content Area */}
        <div className="flex-1 overflow-hidden">
          {currentView === "tables" && <TableManagement />}
          {currentView === "menu" && (
            <div className="h-full overflow-y-auto p-6">
              <MenuGrid />
            </div>
          )}
          {currentView === "dashboard" && (
            <div className="p-6">
              <h1 className="text-3xl font-bold">Dashboard</h1>
              <p className="text-gray-600 mt-2">Coming soon...</p>
            </div>
          )}
          {currentView === "menu-onboarding" && (
            <MenuOnboarding tenantId="default-tenant" />
          )}
        </div>
      </div>

      {/* Right Panel - Order (only on menu view) */}
      {currentView === "menu" && <OrderPanel />}

      {/* Table Selector Modal */}
      <TableSelector
        isOpen={showTableSelector}
        onClose={() => setShowTableSelector(false)}
        onSelectTable={handleTableSelect}
      />
    </div>
  );
}

export default App;
