import React from 'react';
import ReactDOM from 'react-dom/client';
import { getCurrentPlatform } from './lib/platform';
import App from './App';
import AppWeb from './App.web';
import './index.css';

// Detect platform and load appropriate app
const platform = getCurrentPlatform();
console.log('[Main] Platform detected:', platform);

// Select the appropriate App component based on platform
const AppComponent = platform === 'web' ? AppWeb : App;

ReactDOM.createRoot(document.getElementById('root') as HTMLElement).render(
  <React.StrictMode>
    <AppComponent />
  </React.StrictMode>
);
