/**
 * POS Dashboard V2
 * Full-screen Point of Sale interface with neomorphic design
 */

import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuthStore } from '../stores/authStore';
import { usePOSStore } from '../stores/posStore';
import { useNotificationStore } from '../stores/notificationStore';
import { MenuItem, MenuCategory, OrderType } from '../types/pos';
import { AppShell } from '../components/layout-v2/AppShell';
import { NeoCard } from '../components/ui-v2/NeoCard';
import { NeoButton } from '../components/ui-v2/NeoButton';
import { MenuItemCard } from '../components/pos/MenuItemCard';
import { CartItemCard } from '../components/pos/CartItemCard';
import { ModifierModal } from '../components/pos/ModifierModal';
import { CheckoutModal } from '../components/pos/CheckoutModal';
import { cn } from '../lib/utils';

export default function POSDashboard() {
  const navigate = useNavigate();
  const { user, logout } = useAuthStore();
  const {
    cart,
    selectedCategory,
    searchQuery,
    orderType,
    tableNumber,
    notes,
    setSelectedCategory,
    setSearchQuery,
    setOrderType,
    setTableNumber,
    setNotes,
    addToCart,
    removeFromCart,
    updateQuantity,
    clearCart,
    submitOrder,
    getCartTotal,
    getFilteredMenu,
  } = usePOSStore();
  const { playSound } = useNotificationStore();

  const [selectedMenuItem, setSelectedMenuItem] = useState<MenuItem | null>(null);
  const [isModifierModalOpen, setIsModifierModalOpen] = useState(false);
  const [isCheckoutModalOpen, setIsCheckoutModalOpen] = useState(false);

  const filteredMenu = getFilteredMenu();
  const totals = getCartTotal();

  const categories: { id: MenuCategory | 'all'; label: string; icon: string }[] = [
    { id: 'all', label: 'All', icon: '' },
    { id: 'appetizers', label: 'Appetizers', icon: '' },
    { id: 'mains', label: 'Mains', icon: '' },
    { id: 'sides', label: 'Sides', icon: '' },
    { id: 'desserts', label: 'Desserts', icon: '' },
    { id: 'beverages', label: 'Beverages', icon: '' },
  ];

  const orderTypes: { id: OrderType; label: string; icon: string }[] = [
    { id: 'dine-in', label: 'Dine-in', icon: '' },
    { id: 'takeout', label: 'Takeout', icon: '' },
    { id: 'delivery', label: 'Delivery', icon: '' },
  ];

  // Handle menu item click
  const handleMenuItemClick = (item: MenuItem) => {
    setSelectedMenuItem(item);
    setIsModifierModalOpen(true);
  };

  // Handle add to cart
  const handleAddToCart = (
    menuItem: MenuItem,
    quantity: number,
    modifiers: any[],
    specialInstructions?: string
  ) => {
    addToCart(menuItem, quantity, modifiers, specialInstructions);
    playSound('order_ready');
  };

  // Handle checkout
  const handleCheckout = async (paymentMethod: any) => {
    if (!user?.tenantId) return;

    try {
      const order = await submitOrder(user.tenantId, paymentMethod);
      console.log('Order submitted:', order);
      playSound('order_ready');
      // Show success message
      alert(`Order ${order.orderNumber} placed successfully!`);
    } catch (error) {
      console.error('Failed to submit order:', error);
      alert('Failed to submit order. Please try again.');
    }
  };

  // Navigation items
  const navItems = [
    {
      id: 'pos',
      label: 'POS',
      icon: '',
      path: '/pos',
    },
    {
      id: 'aggregator',
      label: 'Orders',
      icon: '',
      path: '/aggregator',
    },
    {
      id: 'kitchen',
      label: 'Kitchen',
      icon: '',
      path: '/kitchen',
    },
    {
      id: 'manager',
      label: 'Manager',
      icon: '',
      path: '/manager',
    },
  ];

  return (
    <AppShell
      navItems={navItems}
      activeNavId="pos"
      onNavigate={(id, path) => navigate(path)}
      className="overflow-hidden"
    >
      <div className="h-full flex flex-col max-w-screen-2xl mx-auto">
        {/* Header */}
        <div className="flex-shrink-0 p-4 border-b border-border">
          <div className="flex items-center justify-between mb-4">
            <div>
              <h1 className="text-2xl font-bold text-foreground">Point of Sale</h1>
              <p className="text-sm text-muted-foreground mt-1">
                {user?.name}  {new Date().toLocaleDateString()}
              </p>
            </div>
            <NeoButton variant="ghost" size="sm" onClick={logout}>
              Logout
            </NeoButton>
          </div>

          {/* Search */}
          <div className="mb-3">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="Search menu items..."
              className="w-full px-4 py-2 rounded-lg neo-inset bg-background text-foreground placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-primary/30"
            />
          </div>

          {/* Categories */}
          <div className="flex gap-2 overflow-x-auto pb-1">
            {categories.map((category) => (
              <button
                key={category.id}
                onClick={() => setSelectedCategory(category.id)}
                className={cn(
                  'pill-nav flex-shrink-0 flex items-center gap-1.5',
                  selectedCategory === category.id && 'pill-nav-active'
                )}
              >
                <span>{category.icon}</span>
                <span>{category.label}</span>
              </button>
            ))}
          </div>
        </div>

        {/* Main Content */}
        <div className="flex-1 flex gap-4 p-4 overflow-hidden">
          {/* Menu Grid - Left Side (2/3) */}
          <div className="flex-[2] overflow-y-auto">
            {filteredMenu.length === 0 ? (
              <NeoCard className="text-center py-12">
                <div className="text-muted-foreground">
                  <div className="text-4xl mb-2"></div>
                  <div>No items found</div>
                  {searchQuery && (
                    <button
                      onClick={() => setSearchQuery('')}
                      className="text-primary underline text-sm mt-2"
                    >
                      Clear search
                    </button>
                  )}
                </div>
              </NeoCard>
            ) : (
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4 pb-4">
                {filteredMenu.map((item) => (
                  <MenuItemCard
                    key={item.id}
                    item={item}
                    onAddToCart={handleMenuItemClick}
                  />
                ))}
              </div>
            )}
          </div>

          {/* Cart Panel - Right Side (1/3) */}
          <div className="flex-1 flex flex-col overflow-hidden">
            <NeoCard variant="raised" className="flex-1 flex flex-col overflow-hidden">
              <div className="p-4 border-b border-border">
                <h2 className="text-lg font-bold text-foreground mb-3">Current Order</h2>

                {/* Order Type */}
                <div className="flex gap-2 mb-3">
                  {orderTypes.map((type) => (
                    <button
                      key={type.id}
                      onClick={() => setOrderType(type.id)}
                      className={cn(
                        'flex-1 px-3 py-2 rounded-lg text-xs font-medium transition-all',
                        orderType === type.id
                          ? 'neo-pressed bg-primary/10 border border-primary/30 text-primary'
                          : 'neo-raised-sm text-muted-foreground'
                      )}
                    >
                      <div>{type.icon}</div>
                      <div>{type.label}</div>
                    </button>
                  ))}
                </div>

                {/* Table Number (Dine-in only) */}
                {orderType === 'dine-in' && (
                  <div>
                    <label className="block text-xs font-medium text-muted-foreground mb-1">
                      Table Number
                    </label>
                    <input
                      type="number"
                      value={tableNumber || ''}
                      onChange={(e) => setTableNumber(e.target.value ? parseInt(e.target.value) : null)}
                      placeholder="Enter table number"
                      className="w-full px-3 py-2 rounded-lg neo-inset bg-background text-foreground text-sm focus:outline-none focus:ring-2 focus:ring-primary/30"
                    />
                  </div>
                )}
              </div>

              {/* Cart Items */}
              <div className="flex-1 overflow-y-auto p-4">
                {cart.length === 0 ? (
                  <div className="h-full flex items-center justify-center text-center">
                    <div className="text-muted-foreground">
                      <div className="text-4xl mb-2"></div>
                      <div className="text-sm">Cart is empty</div>
                      <div className="text-xs mt-1">Add items to get started</div>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {cart.map((item) => (
                      <CartItemCard
                        key={item.id}
                        item={item}
                        onUpdateQuantity={updateQuantity}
                        onRemove={removeFromCart}
                      />
                    ))}
                  </div>
                )}
              </div>

              {/* Cart Footer */}
              {cart.length > 0 && (
                <div className="p-4 border-t border-border space-y-3">
                  {/* Totals */}
                  <div className="neo-inset p-3 rounded-lg space-y-1 text-sm">
                    <div className="flex justify-between text-muted-foreground">
                      <span>Subtotal</span>
                      <span>{totals.subtotal.toFixed(2)}</span>
                    </div>
                    <div className="flex justify-between text-muted-foreground">
                      <span>Tax (5%)</span>
                      <span>{totals.tax.toFixed(2)}</span>
                    </div>
                    <div className="flex justify-between font-bold text-foreground pt-1 border-t border-border">
                      <span>Total</span>
                      <span className="text-primary">{totals.total.toFixed(2)}</span>
                    </div>
                  </div>

                  {/* Actions */}
                  <div className="flex gap-2">
                    <NeoButton
                      variant="ghost"
                      size="sm"
                      onClick={clearCart}
                      className="flex-1"
                    >
                      Clear
                    </NeoButton>
                    <NeoButton
                      variant="primary"
                      size="md"
                      onClick={() => setIsCheckoutModalOpen(true)}
                      className="flex-[2]"
                    >
                      Checkout
                    </NeoButton>
                  </div>
                </div>
              )}
            </NeoCard>
          </div>
        </div>
      </div>

      {/* Modals */}
      <ModifierModal
        isOpen={isModifierModalOpen}
        onClose={() => {
          setIsModifierModalOpen(false);
          setSelectedMenuItem(null);
        }}
        menuItem={selectedMenuItem}
        onAddToCart={handleAddToCart}
      />

      <CheckoutModal
        isOpen={isCheckoutModalOpen}
        onClose={() => setIsCheckoutModalOpen(false)}
        subtotal={totals.subtotal}
        tax={totals.tax}
        total={totals.total}
        orderType={orderType}
        tableNumber={tableNumber}
        onSubmit={handleCheckout}
      />
    </AppShell>
  );
}
