export const usePOSStore = create<POSStore>((set, get) => ({
  // Initial state
  menuItems: MOCK_MENU,
  selectedCategory: 'all',
  searchQuery: '',
  cart: [],
  orderType: 'dine-in',
  tableNumber: null,
  notes: '',
  currentOrder: null,
  recentOrders: [],
  isLoading: false,
  error: null,

  // Menu actions
  setMenuItems: (items) => set({ menuItems: items }),
  setSelectedCategory: (category) => set({ selectedCategory: category }),
  setSearchQuery: (query) => set({ searchQuery: query }),

  fetchMenu: async (tenantId) => {
    try {
      set({ isLoading: true, error: null });
      // In production: const { items } = await backendApi.getMenuItems(tenantId);
      // For now, use mock data
      set({ menuItems: MOCK_MENU, isLoading: false });
    } catch (error) {
      console.error('[POSStore] Failed to fetch menu:', error);
      set({
        error: error instanceof Error ? error.message : 'Failed to fetch menu',
        isLoading: false,
      });
    }
  },

  // Cart actions
  addToCart: (menuItem, quantity, modifiers, specialInstructions) => {
    const cartItemId = `cart-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    const modifiersTotal = modifiers.reduce((sum, mod) => sum + mod.price, 0);
    const subtotal = (menuItem.price + modifiersTotal) * quantity;

    const newCartItem: CartItem = {
      id: cartItemId,
      menuItem,
      quantity,
      modifiers,
      specialInstructions,
      subtotal,
    };

    set((state) => ({
      cart: [...state.cart, newCartItem],
    }));
  },

  removeFromCart: (cartItemId) => {
    set((state) => ({
      cart: state.cart.filter((item) => item.id !== cartItemId),
    }));
  },

  updateQuantity: (cartItemId, quantity) => {
    if (quantity <= 0) {
      get().removeFromCart(cartItemId);
      return;
    }

    set((state) => ({
      cart: state.cart.map((item) => {
        if (item.id === cartItemId) {
          const modifiersTotal = item.modifiers.reduce((sum, mod) => sum + mod.price, 0);
          const subtotal = (item.menuItem.price + modifiersTotal) * quantity;
          return { ...item, quantity, subtotal };
        }
        return item;
      }),
    }));
  },

  updateModifiers: (cartItemId, modifiers) => {
    set((state) => ({
      cart: state.cart.map((item) => {
        if (item.id === cartItemId) {
          const modifiersTotal = modifiers.reduce((sum, mod) => sum + mod.price, 0);
          const subtotal = (item.menuItem.price + modifiersTotal) * item.quantity;
          return { ...item, modifiers, subtotal };
        }
        return item;
      }),
    }));
  },

  clearCart: () => {
    set({
      cart: [],
      orderType: 'dine-in',
      tableNumber: null,
      notes: '',
    });
  },

  // Order actions
  setOrderType: (type) => set({ orderType: type }),
  setTableNumber: (tableNumber) => set({ tableNumber }),
  setNotes: (notes) => set({ notes }),

  submitOrder: async (tenantId, paymentMethod) => {
    const { cart, orderType, tableNumber, notes } = get();
    const totals = get().getCartTotal();

    if (cart.length === 0) {
      throw new Error('Cart is empty');
    }

    const order: Order = {
      orderType,
      tableNumber,
      items: cart,
      subtotal: totals.subtotal,
      tax: totals.tax,
      discount: 0,
      total: totals.total,
      paymentMethod,
      status: 'pending',
      notes,
      createdAt: new Date().toISOString(),
    };

    try {
      set({ isLoading: true, error: null });

      // In production: const { order: createdOrder } = await backendApi.createOrder(tenantId, order);
      // For now, mock the response
      const createdOrder: Order = {
        ...order,
        id: `order-${Date.now()}`,
        orderNumber: `#${Math.floor(1000 + Math.random() * 9000)}`,
      };

      console.log('[POSStore] Order submitted:', createdOrder);

      set((state) => ({
        currentOrder: createdOrder,
        recentOrders: [createdOrder, ...state.recentOrders].slice(0, 10),
        isLoading: false,
      }));

      // Clear cart after successful order
      get().clearCart();

      return createdOrder;
    } catch (error) {
      console.error('[POSStore] Failed to submit order:', error);
      set({
        error: error instanceof Error ? error.message : 'Failed to submit order',
        isLoading: false,
      });
      throw error;
    }
  },

  // Computed
  getCartTotal: () => {
    const { cart } = get();
    const subtotal = cart.reduce((sum, item) => sum + item.subtotal, 0);
    const tax = subtotal * 0.05; // 5% tax
    const total = subtotal + tax;

    return { subtotal, tax, total };
  },

  getFilteredMenu: () => {
    const { menuItems, selectedCategory, searchQuery } = get();

    let filtered = menuItems.filter((item) => item.available);

    // Filter by category
    if (selectedCategory !== 'all') {
      filtered = filtered.filter((item) => item.category === selectedCategory);
    }

    // Filter by search query
    if (searchQuery) {
      const query = searchQuery.toLowerCase();
      filtered = filtered.filter(
        (item) =>
          item.name.toLowerCase().includes(query) ||
          item.description?.toLowerCase().includes(query) ||
          item.tags?.some((tag) => tag.toLowerCase().includes(query))
      );
    }

    return filtered;
  },
}));
